    #cboxContent{background:rgba(#272727, .8); overflow:visible;}
        .cboxIframe{background:rgba(#272727, .8)}
        #cboxError{padding:50px; border:1px solid #ccc;}
        #cboxLoadedContent{margin-bottom:0px;}
        #cboxLoadingOverlay{background: transparent}
        #cboxLoadingGraphic{background: rgba(#272727, .8) url("../img/loading.gif") center/80px no-repeat;}
        #cboxTitle{position:absolute; bottom:-25px; left:0; text-align:center; width:100%; font-weight:bold; color:#7C7C7C;}
        #cboxCurrent{position:absolute; bottom:-25px; left:58px; font-weight:bold; color:#7C7C7C;}

        /* these elements are buttons, and may need to have additional styles reset to avoid unwanted base styles */
        #cboxPrevious, #cboxNext, #cboxSlideshow, #cboxClose {border:0; padding:0; margin:0; overflow:visible;  position:absolute; bottom:-29px; width:23px; height:23px; text-indent:-9999px;}

        /* avoid outlines on :active (mouseclick), but preserve outlines on :focus (tabbed navigating) */
        #cboxPrevious:active, #cboxNext:active, #cboxSlideshow:active, #cboxClose:active {outline:0;}

        #cboxPrevious{left:0px; background-position: -51px -25px;}
        #cboxPrevious:hover{background-position:-51px 0px;}
        #cboxNext{left:27px; background-position:-75px -25px;}
        #cboxNext:hover{background-position:-75px 0px;}
        #cboxClose {
            position: absolute;
            right:0;
            top: 0;
            width: 60px;
            height: 60px;
            background: transparent;
            z-index: 2;

            &:after, &:before {
                content: "";
                position: absolute;
                top: 50%;
                left: 50%;
                width: 70%;
                height: 3px;
                background-color: #272727;
                opacity: 0.7;
                transition: opacity 0.3s;
            }

            &:before {
                transform: translate(-50%, -50%) rotate(45deg);
            }

            &:after {
                transform: translate(-50%, -50%) rotate(-45deg);
            }
        }
        #cboxClose:hover{
            &:before, &:after {
                opacity: 1;
            }
        }

        .cboxSlideshow_on #cboxSlideshow{background-position:-125px 0px; right:27px;}
        .cboxSlideshow_on #cboxSlideshow:hover{background-position:-150px 0px;}
        .cboxSlideshow_off #cboxSlideshow{background-position:-150px -25px; right:27px;}
        .cboxSlideshow_off #cboxSlideshow:hover{background-position:-125px 0px;}