  position: fixed;
}
.header__burger span {
  display: block;
  position: absolute;
  height: 2.5px;
  width: 100%;
  border-radius: 15px;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: 0.25s ease-in-out;
  background-color: #6ca462;
}
